<template>
  <div>
    <el-select v-model="choice" @change="changeDate">
      <el-option
        v-for="item in date"
        :key="item.way"
        :label="item.label"
        :value="item.way"
      >
      </el-option>
    </el-select>
    <div class="business_loading" v-show="isLoading"></div>
    <div class="business_wrapper" v-show="!isLoading">
      <el-table
        class="bussinesss_table"
        border
        :data="bussinessStatistics"
        cell-class-name="bussiness_cell"
        header-row-class-name="bussiness_head"
        style="width: 100%"
        height="100%"
      >
        <el-table-column
          prop="total_order_price"
          :label="$t('data.turnOver')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="valid_order_sum"
          :label="$t('data.validOrder')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="invalid_order_sum"
          :label="$t('data.invalidOrder')"
          width="width"
        >
        </el-table-column>
        <el-table-column
          prop="all_order_sum"
          :label="$t('data.totalOrder')"
          width="width"
        >
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { startLoading, endLoading } from "@/util/loading.js";
export default {
  mounted() {
    startLoading(".business_loading");
    let obj = {
      way: this.choice,
      pay_method: 1,
      shop_id: this.shopId,
    };
    this.getShopStatistics(obj).then(() => {
      this.isLoading = false;
      endLoading();
    });
  },
  data() {
    return {
      isLoading: true,
      date: [
        {
          way: "day",
          label: this.$t("data.today"),
        },
        {
          way: "week",
          label: this.$t("data.week"),
        },
        {
          way: "month",
          label: this.$t("data.month"),
        },
        {
          way: "all",
          label: this.$t("data.all"),
        },
      ],
      choice: "day",
    };
  },
  methods: {
    changeDate(value) {
      this.isLoading = true;
      startLoading(".business_loading");
      let obj = {
        way: value,
        pay_method: 1,
        shop_id: this.shopId,
      };
      this.getShopStatistics(obj).then(() => {
        this.isLoading = false;
        endLoading();
      });
    },
    ...mapActions({
      getAllSales: "statistics/sGetAllSales",
      getShopStatistics: "statistics/sGetShopStatistics",
    }),
  },
  computed: {
    ...mapState({
      bussinessStatistics: (state) => state.statistics.bussinessStatistics,
      shopId: (state) => state.user.data.role_id,
    }),
  },
};
</script>

<style lang="scss" scoped>
.business_wrapper {
  height: calc(100vh - 200px);
}

.business_loading {
  padding-top: 4em;
  height: 200px;
}

.bussinesss_table {
  margin-top: 3em;
}

::v-deep .bussiness_cell {
  height: 200px;
  text-align: center;
  font-size: 36px;

  .cell {
    line-height: 36px;
  }
}

::v-deep .bussiness_head {
  font-weight: bold;
  color: $--color-primary;

  .cell {
    text-align: center;
  }
}
</style>
